import Home from './pages/HomePage/HomePage';
import About from './pages/AboutPage/AboutPage';
import PetsPage from './pages/PetsPage/PetsPage';
import PetPage from './pages/PetPage/PetPage';

const routes = [
  {
    path: '/',
    exact: true,
    component: Home,
  },
  {
    path: '/about',
    exact: true,
    component: About,
  },
  {
    path: '/pets/:id',
    exact: true,
    component: PetPage,
  },
  {
    path: '/pets',
    exact: true,
    component: PetsPage,
  },
  {
    component: Home,
  },
];
export default routes;
